.HeaderWrapper {
  background: #dadae7;
  position: relative;
  height: 50px;
  display: flex;
  border-bottom: 1px solid #f0f0f0;
}
.HeaderWrapper .header-title {
  height: 100%;
  line-height: 50px;
  font-size: 25px;
  font-family: 'KaiTi';
  font-weight: bold;
  margin: 0 35px;
  color: rgba(14, 75, 187, 0.8);
}
.HeaderWrapper .header-login {
  margin: 6px 0;
  height: 36px;
  border-radius: 18px;
  position: absolute;
  right: 40px;
  background: #ea6f5a;
  color: white;
}
.HeaderWrapper .header-input {
  width: 300px;
  height: 35px;
  line-height: 35px;
  border-radius: 17px;
  margin: 7px 0;
}
.HeaderWrapper .menu {
  height: 50px;
  margin: 0 50px;
  font-size: 14px;
  width: 400px;
  background: #dadae7;
}
.HeaderWrapper .menu .nav-text {
  font-size: 15px;
}
